.calendar-header {
    display: flex;
    border-bottom: 1px solid #e0e0e0;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 100;
    flex-shrink: 0;
}

.time-column-header {
    height: 100px;
    width: 60px;
    min-width: 60px;
    border-right: 1px solid #e0e0e0;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 101;
}

.header-content {
    display: flex;
    flex: 1;
}

.header-item {
    display: flex;
    gap: 6px;
    flex: 1;
    text-align: center;
    padding: 6px 0;
    border-right: 1px solid #e0e0e0;
    flex-direction: column;
    justify-content: center;
}

.day-name {
    font-size: 1.6rem;
    color: #8466D7;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 5px;
}

.day-number {
    font-size: 1.4rem;
    color: #8466D7;
    margin-top: 2px;
    margin-bottom: 0;
}

.today {
    background-color: #F3EFFF;
}

.today .day-name,
.today .day-number {
    font-weight: 600;
}

.grid-container {
    display: flex;
    flex: 1;
    overflow: auto;
    height: calc(100% - 110px);
    position: relative;
}

.time-column {
    width: 60px;
    min-width: 60px;
    border-right: 1px solid #e0e0e0;
    position: sticky;
    left: 0;
    background-color: #fff;
    z-index: 99;
    min-height: 1463px;
}

.time-slot-container {
    height: 60px;
    position: relative;
    border-bottom: 1px solid #e0e0e0;
}

.time-slot-container:last-child {
    border-bottom: none;
}

.time-slot-label {
    position: absolute;
    right: 12px;
    top: -10px;
    font-size: 1rem;
    color: #8466D7;
    background-color: #fff;
    padding: 0 4px;
}

.time-slot-label.hidden {
    color: transparent;
}

.time-grid {
    display: flex;
    flex: 1;
}

.day-column {
    flex: 1;
    border-right: 1px solid #e0e0e0;
    min-height: 1463px;
    position: relative;
}

.time-cell {
    height: 60px;
    min-height: 60px;
    border-bottom: 1px solid #e0e0e0;
    position: relative;
    cursor: pointer;
    overflow: visible;
}

.time-cell:last-child {
    border-bottom: none;
}

.midnight-cell {
    border-top: none;
    border-bottom: 1px solid #e0e0e0;
}

.time-cell:hover {
    background-color: #f8f8f8;
}

.today-column .time-cell:hover {
    background-color: #F8F6FF;
}

.current-time-indicator {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #8466D7;
    z-index: 100;
}

.current-time-indicator::before {
    content: "";
    position: absolute;
    left: -5px;
    top: -4px;
    width: 10px;
    height: 10px;
    background-color: #8466D7;
    border-radius: 50%;
}